import React from 'react';
import {HashRouter,Route } from 'react-router-dom'  
import Home from "./route/Home"
import About from './route/About';
function App() {
  return (
        <HashRouter>
          <Route exact path="/">
            <Home></Home>
          </Route>
          <Route exact path="/index">
            <Home></Home>
          </Route>
          <Route exact path="/about">
            <About></About>
          </Route>
        </HashRouter>
  );
}

export default App;
